.loader {
  color: #3bb15e;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 32px;
  display: flex;
}

.loader img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  text-align: center;
  margin-top: 20px;
  display: none;
}

.cat-info img {
  width: 500px;
  height: 400px;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.cat-info h2 {
  margin: 10px 0;
  font-size: 24px;
}

.cat-info p {
  color: #444;
  margin: 10px 0;
  font-size: 18px;
}

.error {
  color: #ff5733;
  margin-top: 20px;
  font-size: 18px;
  display: none;
}

.breed-select {
  background: #a19696;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 8px;
  font-size: 18px;
}

.input-container {
  justify-content: flex-end;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.input-label {
  color: #444;
  margin-right: 10px;
  font-size: 18px;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 8px 20px;
  font-size: 18px;
  transition: background-color .3s;
}

.submit-btn:hover {
  background-color: #2980b9;
}

